@import "variables.scss";

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: "Bertioga Sans", serif;
}

html {
	@include for-size(mobile, up) {
		font-size: 14px; /*i.e. = 1rem*/
	}
	@include for-size(mobile, down) {
		font-size: 16px; /*i.e. = 1rem*/
	}
	@include for-size(ultrahd, up) {
		font-size: 18px; /*i.e. = 1rem*/
	}
}

body {
	font-family: "Open Sans", sans-serif !important;
}

//Define sass mixins for headings and Body
@mixin body-style {
	/*ON mobile screen*/
	@include for-size(mobile, down) {
		font-size: 1rem;
		line-height: 1.4;
	}
	/*ON tablet screen*/
	@include for-size(mobile, up) {
		font-size: 1.15rem;
		line-height: 1.375;
	}
	/*ON desktop screen*/
	@include for-size(tablet, up) {
		font-size: 1.3rem;
		line-height: 1.4;
	}
	/*ON ultrahd screen*/
	@include for-size(ultrahd, up) {
		font-size: 1.4rem;
		line-height: 1.5;
	}
	/*ON 4k screen*/
	@include for-size(4k, up) {
		font-size: 1.6rem;
		line-height: 1.6;
	}
}

@mixin h1-style {
	/*ON mobile screen*/
	@include for-size(mobile, down) {
		font-size: 1.8rem;
		line-height: 1.25;
	}
	/*ON tablet screen*/
	@include for-size(mobile, up) {
		font-size: 2.5rem;
		line-height: 1.125;
	}
	/*ON desktop screen*/
	@include for-size(tablet, up) {
		font-size: 2.25rem;
		line-height: 1.2;
	}
	/*ON ultrahd screen*/
	@include for-size(ultrahd, up) {
		font-size: 2.75rem;
		line-height: 1.15;
	}
	/*ON 4k screen*/
	@include for-size(4k, up) {
		font-size: 3rem;
		line-height: 1.25;
	}
}

@mixin h2-style {
	/*ON mobile screen*/
	@include for-size(mobile, down) {
		font-size: 1.525rem;
		line-height: 1.15384615;
	}
	/*ON tablet screen*/
	@include for-size(mobile, up) {
		font-size: 1.8rem;
		line-height: 1.25;
	}
	/*ON desktop screen*/
	@include for-size(tablet, up) {
		font-size: 1.85rem;
		line-height: 1.25;
	}
	/*ON ultrahd screen*/
	@include for-size(ultrahd, up) {
		font-size: 2rem;
		line-height: 1.325;
	}
	/*ON 4k screen*/
	@include for-size(4k, up) {
		font-size: 2.5rem;
		line-height: 1.25;
	}
}

@mixin h3-style {
	/*ON mobile screen*/
	@include for-size(mobile, down) {
		font-size: 1.15rem;
		line-height: 1.1;
	}
	/*ON tablet screen*/
	@include for-size(mobile, up) {
		font-size: 1.375rem;
		line-height: 1.25;
	}
	/*ON desktop screen*/
	@include for-size(tablet, up) {
		font-size: 1.5rem;
		line-height: 1.25;
	}
	/*ON ultrahd screen*/
	@include for-size(ultrahd, up) {
		font-size: 1.75rem;
		line-height: 1.25;
	}
	/*ON 4k screen*/
	@include for-size(4k, up) {
		font-size: 2rem;
		line-height: 1.25;
	}
}

@mixin h4-style {
	/*ON mobile screen*/
	@include for-size(mobile, down) {
		font-size: 1rem;
		line-height: 1;
	}
	/*ON tablet screen*/
	@include for-size(mobile, up) {
		font-size: 1.125rem;
		line-height: 1.15;
	}
	/*ON desktop screen*/
	@include for-size(tablet, up) {
		font-size: 1.25rem;
		line-height: 1.15;
	}
	/*ON ultrahd screen*/
	@include for-size(ultrahd, up) {
		font-size: 1.5rem;
		line-height: 1.15;
	}
	/*ON 4k screen*/
	@include for-size(4k, up) {
		font-size: 1.75rem;
		line-height: 1.25;
	}
}

@mixin blockquote-style {
	/*ON mobile screen*/
	@include for-size(mobile, down) {
		font-size: 1.1rem;
		line-height: 1.35;
	}
	/*ON tablet screen*/
	@include for-size(mobile, up) {
		font-size: 1.1rem;
		line-height: 1.475;
	}
	/*ON desktop screen*/
	@include for-size(tablet, up) {
		font-size: 1.4rem;
		line-height: 1.5;
	}
	/*ON ultrahd screen*/
	@include for-size(ultrahd, up) {
		font-size: 1.5rem;
		line-height: 1.6;
	}
	/*ON 4k screen*/
	@include for-size(4k, up) {
		font-size: 1.7rem;
		line-height: 1.7;
	}
}

body {
	@include body-style;
}

h1 {
	@include h1-style;
	font-weight: bold;
}

h2 {
	@include h2-style;
	font-weight: 600;
}

h3 {
	@include h3-style;
	font-weight: 500;
}

h4 {
	@include h4-style;
	font-weight: 500;
}

blockquote {
	@include blockquote-style;
}
